import React, { Component } from 'react';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import { connect } from 'react-redux';
import { showHotspot } from '../../../../actions/themeActions';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../utils'
import Image from './Media/Image'

import Tiktoks from './tiktok'
import Hotspot from '../../../Themes/Elements/HotSpot';
import { SET_MEDIA_SIZE } from '../../../../reducers/reducersKeys';

class PopUpMedia extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isHotspot: false,
            loadedImg: false,
            mediaLoaded: false,
            imgUrl: props.ImageUrl
        }
        this.myRef = React.createRef();
    }

    componentWillReceiveProps(nextProps) {
        const { ImageUrl } = nextProps;
        if (this.state.imgUrl !== ImageUrl) {
            this.setState({ imgUrl: ImageUrl, mediaLoaded: false })
        }
    }

    updateMediaSize = (e) => {
        const { setMediaSize } = this.props;
        const mediaHeight = e.target.offsetHeight
        const mediaWidth = e.target.offsetHeight
        setMediaSize({ mediaHeight, mediaWidth })
        this.setState({ mediaLoaded: true })
    }


    render() {
        const { item, instaImg, ugc_products, isVideoError, wall, custmClass, ImageUrl, videoUrl, active, showHotspot, mediaWidth, mediaHeight } = this.props;
        const { loadedImg, mediaLoaded } = this.state;
        return (
            <div className="tb_media_holder" key={`postID${item.id}`}>
                {active ?
                    // <div className={custmClass}>
                    <>
                        {(item.type === 2 || item.type === 4) ? 
                           <div className={mediaLoaded ?'' :'sk_loader_product'}>
                            <div className="tb_img_holder" ref={this.myRef} onMouseEnter={() => showHotspot({ showAll: true })} onMouseLeave={() => showHotspot({ showAll: false })} style={{ minHeight: !mediaLoaded ? mediaHeight : null, minWidth: !mediaLoaded ? mediaWidth : null }}>
                                        {(item.type == 2 || item.type == 4) && (item.hotspot == 1 && item.ugc_personalizaion.UgcSetting.status == 1) ? <Hotspot item={item} ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} /> : null}
                                            <img src={ImageUrl} data-src={ImageUrl} onLoad={(e) => this.setState({ loadedImg: true }, () => this.updateMediaSize(e))} onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => { })
                                            } data-network={item.networkId} data-filter-id={item.filterId} data-stories={item.stories} data-load="0" data-item-id={item.id} data-wall-id={wall.Wall.id} alt='popupImg' />
                                            {!loadedImg ? <div className="sk_loader_product"><div></div></div>  : <> </> }
                            </div>
                            </div>

                            : item.type === 1 ? <></>
                                : (item.type === 3 || item.type === 5) ?

                                    <>
                                        <div className="tb_img_holder" style={{ minHeight: !mediaLoaded ? mediaHeight : null, minWidth: !mediaLoaded ? mediaWidth : null }}>
                                            <img src={ImageUrl} style={{ opacity: 0 }} onLoad={(e) => this.setState({ loadedImg: true }, () => this.updateMediaSize(e))} />
                                        </div>
                                        <div className="tb_video_holder" style={{ minHeight: !mediaLoaded ? mediaHeight : null, minWidth: !mediaLoaded ? mediaWidth : null , backgroundColor:'#000'}} >
                                            <ReactPlayer
                                                style={{ display: 'contents' }}
                                                className="tb-detail-image-iframe"
                                                url={videoUrl}
                                                data-type="video"
                                                config={{
                                                    facebook: {
                                                        appId: '117029975354072',
                                                        attributes: {
                                                            'data-width': 'auto',
                                                            // 'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                                                        }
                                                    }
                                                }}
                                                data-stories={item.stories}
                                                data-filter-id={item.filterId}
                                                data-network={item.networkId}
                                                data-link={item.link}
                                                data-wall-id={wall.Wall.id} data-item-id={item.id}
                                                data-load={0}
                                                onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e) }}
                                                autoPlay={true}
                                                muted={true}
                                                playsinline={true}
                                                loop={true}
                                                playing={true}
                                                controls
                                            />
                                            
                                        </div>
                                    </>
                                    : <></>
                        }

                    </> :
                    <div className="tb_img_holder" ref={this.myRef}>
                        <img src={ImageUrl} data-src={ImageUrl} onLoad={(e) => this.setState({ loadedImg: true }, () => this.updateMediaSize(e))} onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => { })
                        } data-network={item.networkId} data-filter-id={item.filterId} data-stories={item.stories} data-load="0" data-item-id={item.id} data-wall-id={wall.Wall.id} alt='popupImg' />
                        {(item.type == 2 || item.type == 4) && (item.hotspot == 1 && item.ugc_personalizaion.UgcSetting.status == 1) ? <Hotspot item={item} ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} /> : null}

                    </div>
                }

            </div>
        );
    }
}

const mapStateToProps = state => ({
    mediaHeight: state.modalPop.mediaHeight,
    mediaWidth: state.modalPop.mediaWidth
})
const mapDispatchToProps = dispatch => {
    return {
        setMediaSize: (data) => dispatch({ type: SET_MEDIA_SIZE, payload: data }),
        showHotspot: (data) => dispatch(showHotspot(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PopUpMedia);